<template>
  <div class="beijinTY">
   <fg-header title="购买守卫者"></fg-header>
   <user-info class="user-info marT40"  ></user-info>
   <div class="marT40">
     <ul class="listul">
       <li class="listLi" v-for="item in listAll" :key="item.id">
         <div class="neibubg">
           <div class="imgdiv">
             <van-image class="listimg" :src="item.image" alt="" />
             <div class="listbtn chufa"><span class="buttext" @click="gobuy(item)">马上购买</span></div>
           </div>
           <div class="listtext">
             <p>名称：{{item.level}}</p>
             <!-- <p>产出：{{item.contribution}}/天</p> -->
              <p>战力：{{item.power}}</p>
             <p>日期： {{item.duration}}天</p>
             <p>价格： {{item.price}}GTF</p>
             <!-- <p class="boxstart">所有者：<span class="one-txt-cut suoyouzhe">agfadghdfa</span> </p> -->
           </div>
         </div>
       </li>
     </ul>
   </div>
     <van-popup v-model="popupshow">
        <div class="yaoqingbg" >
          <h1 class="yaoqingtitle">购买数量</h1>
          <div class="copy_text" >
            <div class="jibuqi"><van-stepper v-model="value" integer min="1" max="9999" input-width="80px" /></div>
            <div>合计：<img class="imgjinbin" src='../../assets/images/jinbi.png'/>{{price*value}}</div>
          </div>
          <div class="btnline">
            <div class="quxiao" @click="popupshow=false">我再想想</div>
            <div class='fuzhi' @click="buy()">马上购买</div>
          </div>
        </div>
      </van-popup>
  </div>
</template>
<script type='text/ecmascript-6'>
import { list_all, buy } from '@/api/guardian'
export default {
  components: {
  },

  data() {
    return {
       popupshow:false,
       value:'',
       listAll:[],
       itemL:'',
       price:0,
    };
  },
  created() {
  },
  mounted() {
     this.int()
  },
  methods: {
 
    int(){
      list_all().then((res) => {
        if(res.data.code=='success'){
          this.listAll=res.data.data
        }else{this.$toast(res.data.msg)}
        
          }).catch((error) => {
            reject(error)
          })
    },
    gobuy(item){
      this.price=item.price
      this.itemL=item
      this.popupshow=true
    },
    buy(){
      let data={
        id:this.itemL.id,
        amount:this.value,
        ...this.$datapublic
      }
       buy(data).then((res) => {
         if(res.data.code=='success'){
            this.popupshow=false
          this.$toast('购买成功')
           this.$router.push({ name:'guardian', params: { title:'我的守卫者'} })
         }else{this.$toast(res.data.msg)}
         
         
          }).catch((error) => {
            this.$toast(error)
            reject(error)
          })
    },
    

  },
  filters: {
       
   },
};
</script>
<style lang="less" scoped>
.yaoqingbg{
  width:532px;
  height:547px;
  background: url('../../assets/images/tanchuang@2x.png') no-repeat 100% 100%;
  background-size: 100% 100%;
  text-align: center;
  color:#fff;
}
.yaoqingtitle{
   line-height: 80px;
  font-weight: bold;
  padding-top: 45px;
  font-size: 48px;
}
.copy_text{
  color:#9F3D0D;
  font-size: 32px;
}
.btnline{
  margin-top:40px;
}
.quxiao{
  width:170px;
  height:75x;
  background: url('../../assets/images/quxiao.png') no-repeat 100% 100%;
  background-size: 100% 100%;
  text-align: center;
  line-height: 80px;
  font-weight: bold;
  display: inline-block;
  margin-right: 1px;
  margin-left: 8px;
}
.fuzhi{
  width:170px;
  height:75px;
  background: url('../../assets/images/fuzhi.png') no-repeat 100% 100%;
  background-size: 100% 100%;
  text-align: center;
  line-height: 80px;
  font-weight: bold;
  display: inline-block;
  margin-left: 14px;
}
.van-popup{
  background-color: transparent !important;
}
.imgjinbin{
  width: 38px;
  height: 38px;
  position: relative;
  top:5px
}
.jibuqi{
  background-color: #EECB78;
  border: 1px solid #D8B463;
  width: 334px;
  height: 57px;
  margin: 90px auto 20px auto;
  padding: 7px 0;
  border-radius: 10px;
}
/deep/.van-stepper__minus--disabled, .van-stepper__plus--disabled,
/deep/.van-stepper__plus,
/deep/.van-stepper__minus{
  background-color:#EECB78;
  border: 1px dashed #CEA056;
  color:#CEA056;
}
/deep/.van-stepper__input{
  background-color:#EECB78;
  color:#9E3A0B;
}

</style>
